import React from 'react';
import FlowComponent from '../../common/component/core/FlowComponent';
import $, { event } from 'jquery';
import {Form, Button } from 'react-bootstrap';
import '../../../styles/flow.css';
import {updateKYCRules, reviewRules} from '../../../actions/kyc_actions';

class KYCProcess extends FlowComponent {
    state = {
        kyc_rules: {}, 
        
    }
    
    componentWillMount(){
        
        const cust_id = this.props.match.params.cust_id;

        reviewRules(this.req({cust_id}))
         .then((response) => {
          	
                console.log(response.data.kyc_rules);
              this.setState({
                kyc_rules: response.data.kyc_rules, cust_id
               }); 
             
           });
            // this.setState({cust_id,rule_groups});
            // var jp = require('jsonpath');
            // var names = jp.query(this.state.rule_groups, "$..rules[?(@.rule_id=='5')]");
    }
    submitKYC = (event) =>{ 
        event.preventDefault()
       
        const req =  this.req({"kyc_rules":this.state.kyc_rules,"cust_id":this.state.cust_id})
        updateKYCRules(req)
       .then((response) => {
        console.log('this.state.kyc_rules')
           console.log(this.state.kyc_rules)
            if(!response){return };
            if(response){
                alert("KYC data updated successfully");
                window.location.href = "/borrower/indiv/view/"+this.state.cust_id;
            }
       }
       );
                
    }
    onChangeKYC = (event) => {
        var jp = require('jsonpath');
        var kyc_rules = this.state.kyc_rules
        
        const jsonpath1 = `$`
        
        var status1 = jp.value(kyc_rules, jsonpath1);
        console.log(status1)

        const jsonpath = `$..[?(@.rule_id=='${event.target.id}')].rule_status`
             
        var status = jp.value(kyc_rules, jsonpath);
        console.log(status)
        if(status == 'on'){
            status = 'off'
        }else{
            status = 'on'
        }
       
        jp.value(kyc_rules, jsonpath, status);
        
        this.setState({kyc_rules});

    }
    render(){
        let status;
       
        return(
            <div className="kyc-container">
                <div className="kyc_heading"><h1><b>KYC Process for {this.state.cust_id}</b></h1></div>
                {Object.keys(this.state.kyc_rules).map((group_name)=>
                    <>
                        <div className="kyc_rule"><b>{group_name}</b></div>
                        <ul>  
                            
                            {this.state.kyc_rules[group_name] && this.state.kyc_rules[group_name].map((rule,i) =>
                            <div className="kyc_rule" style={{padding:'10px'}}  >
                            {rule.rule_status == "on" ? status = true : status =false}
                            <li style={{display:'inline'}}>{rule.rule} 
                            
                            <Form.Check className="toggle_switch" type="switch"  style={{display:'inline',float:"right"}} id={rule.rule_id} label="" onChange={this.onChangeKYC} checked ={status}/><br/>
                            </li> 
                            
                        </div>
                            )}
                            
                        </ul>
            
                    </>
                
                    )
                }
                <Button type='button' onClick={this.submitKYC}>Submit</Button>
            </div>
        
        );
    }
}
export default KYCProcess;